@mixin text-ellipsis($line-clamp: 2) {
	@if ($line-clamp >= 2) {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: $line-clamp;
		-webkit-box-orient: vertical;
	} @else {
		display: block;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}

@mixin icon($icon-name, $wh: 1.6rem, $color1: '000', $color2: '000') {
	display: block;
	width: $wh;
	height: $wh;
	background: icon($icon-name, $color1, $color2) center / 100% no-repeat;
}

@mixin pseudo-icon($icon-name, $wh: 1.6rem, $color1: '000', $color2: '000') {
	position: relative;
	padding-left: $wh;
	&::before {
		content: '';
		display: block;
		width: $wh;
		height: $wh;
		position: absolute;
		left: 0;
		top: calc(50% - ($wh / 2));
		background: icon($icon-name, $color1, $color2) center / 100% no-repeat;
	}
}

@mixin img-crop($width: auto, $height: auto) {
	display: block;
	position: relative;
	width: $width;
	height: $height;
	overflow: hidden;
	img {
		object-fit: cover;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}
}
