// ============================== Font 시작 ==============================
h2,
h3,
h4,
.h2,
.h3,
.h4 {
	font-family: 'ChosunSm', serif;
}
h2,
.h2 {
	position: relative;
	font-size: 3.2rem;
}
h3,
.h3 {
	font-size: 2.4rem;
}
h4,
.h4 {
	font-size: 2rem;
}
.text {
	word-break: break-all;
	text-align: justify;
	text-indent: 1rem;
}
b {
	font-family: 'ChosunKg', sans-serif;
}
// ============================== Font  끝  ==============================

//아이콘
.icon-wifi {
	@include icon(icon-wifi);
}

// ============================== Common 시작 ==============================
#container {
	background: none;
}
[class*='-figure'] {
	.img {
		@include img-crop;
		aspect-ratio: 16/9;
	}
	.caption {
		@include pseudo-icon(icon-up-fill);
		font-size: 1.4rem;
		margin-top: 0.4rem;
	}
}
#root {
	position: relative;
	background: #64646403;
	&::after {
		opacity: 0.8;
		user-select: none;
		pointer-events: none;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: url(../img/paper.png) center top/ 100% auto repeat-y;
	}
	#wrap {
		max-width: 160rem;
		margin: 0 auto;
		padding: 6rem;
	}
}

// 리스트 스타일 시작
[class^='list-style'] {
	li {
		position: relative;
		padding-left: 0.8rem;
		margin-top: 0.4rem;
		&:before {
			display: block;
			position: absolute;
			left: 0;
		}
	}
	&.list-style-bul {
		li {
			&::before {
				content: '';
				width: 0.3rem;
				height: 0.3rem;
				border-radius: 50%;
				background-color: #000;
				top: 0.8rem;
			}
			&.point {
				&::before {
					background-color: #f2f219;
				}
			}
		}
	}
	&.list-style-dash {
		li {
			&::before {
				content: '-';
				width: auto;
				height: auto;
				background: none;
				top: 0;
			}
			&.point {
				&::before {
					background: none;
				}
			}
		}
	}
	&.list-style-star {
		li {
			&::before {
				content: '*';
				width: auto;
				height: auto;
				background: none;
				top: 0;
			}
			&.point {
				&::before {
					background: none;
				}
			}
		}
	}
}
// 리스트 스타일 끝
// ============================== Common  끝  ==============================

// ============================== 타이틀 시작 ==============================
.title {
	.pattern {
		display: flex;
		text-align: center;
		justify-content: space-between;
		align-items: flex-end;
		gap: 1rem;
		p {
			font-weight: bold;
			font-family: 'ChosunKm', serif;
		}
		img {
			flex: 1;
			min-width: 30rem;
		}
	}
	.wrap {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		border-top: 0.2rem solid #000;
		border-bottom: 0.6rem double #000;
		padding: 2rem 0;
		margin-top: 1rem;
		.h1 {
			font-family: 'ChosunKm', serif;
			text-align: center;
			font-size: 8rem;
			text-transform: uppercase;
			flex: 6;
		}
		.ad,
		.date {
			display: flex;
			flex: 1;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			font-family: 'ChosunKg', sans-serif;
		}
		.ad {
			border: 0.6rem double #000;
			padding: 0 4rem;
		}
		.date {
			border: 0.6rem double #000;
			padding: 0 4rem;
			.month {
				text-align: center;
			}
			.year {
				text-align: center;
				font-size: 4rem;
			}
		}
	}
}
// ============================== 타이틀  끝  ==============================

// ============================== 푸터 시작 ==============================
.footer {
	display: flex;
	gap: 2.4rem;
	margin-top: 2.4rem;
	> * {
		padding: 2rem 0;
	}
	.footer-logo {
		flex: 1;
		flex-basis: 300px;
		border: 0.6rem double #000;
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 1rem;
		img {
			max-width: 36px;
		}
		div {
			p {
				font-family: 'ChosunSm', serif;
			}
		}
	}
	.footer-info {
		flex: 3;
		flex-basis: 900px;
		position: relative;
		border: 0.2rem solid #000;
		text-align: center;
		.btn-account {
			position: absolute;
			left: 2rem;
			top: 2rem;
			width: 15%;
			height: calc(100% - 4rem);
			border: 0.2rem solid #000;
		}
	}
	.btn-reservation {
		flex: 1;
		flex-basis: 300px;
		border: 0.6rem double #000;
	}
}
.footer-copy {
	margin-top: 2.4rem;
	border-top: 0.2rem solid #000;
	text-align: center;
	padding-top: 2rem;
}
// ============================== 푸터  끝  ==============================

// ============================== 내비게이션바 시작 ==============================
.gnb {
	.depth1 {
		height: 4rem;
		overflow: hidden;
		display: flex;
		justify-content: space-around;
		border-bottom: 0.2rem solid #000;
		font-family: 'ChosunSm', serif;
		text-align: center;
		transition-duration: 0.5s;
		// &:hover {
		// height: 14.4rem;
		// 4 + (2.4 * 4) + 0.8
		// }
		> li {
			position: relative;
			a {
				display: block;
				width: 12rem;
				height: 4rem;
				position: relative;
				text-transform: uppercase;
				span {
					display: block;
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%);
					transition-duration: 0.5s;
					&.ko {
						opacity: 0;
					}
				}
				&.active,
				&:hover {
					font-family: 'ChosunKm', serif;
					.en {
						opacity: 0;
					}
					.ko {
						opacity: 1;
					}
				}
				// + .depth2 {
				// 	a {
				// 		height: 2.4rem;
				// 	}
				// }
			}
		}
	}
}
// ============================== 내비게이션바  끝  ==============================

// ============================== 팝업 시작 ==============================
.ReactModal__Overlay {
	background-color: #00000090 !important;
}
.modal {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 55vw;
	max-width: 100rem;
	border: solid 0.1rem #5c5c5c;
	padding: 2.4rem;
	display: grid;
	grid-template-areas:
		'modal-title'
		'modal-content'
		'modal-button';
	grid-template-rows: auto 1fr auto;
	gap: 2.4rem;
	background: #a5a5a5;
	&::after {
		opacity: 0.8;
		user-select: none;
		pointer-events: none;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		background: url(../img/paper.png) center/ 100% auto repeat-y;
	}
	.modal-title {
		grid-area: modal-title;
		text-align: center;
		position: relative;
		padding-top: 2.4rem;
		.modal-close {
			@include icon(icon-close, 2.8rem);
			position: absolute;
			right: 0;
			top: 0;
		}
	}
	.modal-content {
		grid-area: modal-content;
		overflow-y: auto;
	}
	.modal-button-area {
		grid-area: modal-button;
		justify-content: center;
		display: flex;
		gap: 0.8rem;
		button {
			max-width: 16rem;
			height: 4.8rem;
			padding: 0.8rem 4rem;
			border: 4px double #000;
		}
		button:first-child:nth-last-child(2),
		button:first-child:nth-last-child(2) ~ button {
			width: 50%;
		}
	}
}
// ============================== 팝업  끝  ==============================

// ============================== 콘텐츠 시작 ==============================
.content {
	display: grid;
	grid-template-columns: 1fr 3fr 1fr;
	gap: 2.4rem;
	padding-top: 4rem;
	> * {
		.title {
			margin-bottom: 1.2rem;
			&.with-viewmore {
				position: relative;
				display: flex;
				justify-content: space-between;
				align-items: center;
				a {
					color: #666;
				}
			}
		}
		.sub-title {
			margin-bottom: 1.2rem;
		}
	}
	> *:not([class*='-figure'], .rooms-text),
	.info-reservation {
		padding-top: 1.6rem;
		border-top: 0.2rem solid #000;
	}

	// ============================== 홈 시작 ==============================
	&.main {
		@keyframes scale {
			from {
				transform: scale(1.1);
			}
			80% {
				transform: scale(1);
			}
			to {
				transform: scale(1);
			}
		}
		grid-template-areas:
			'visual-figure visual-figure visual-text'
			'info rooms-tit rooms-tit'
			'info rooms-figure rooms-text'
			'info around faq'
			'news news news';
		.visual-figure {
			grid-area: visual-figure;
			.img img {
				animation: scale 10s linear 0s infinite forwards;
				transform: scale(1.1);
			}
		}
		.visual-text {
			grid-area: visual-text;
		}

		.rooms-tit {
			grid-area: rooms-tit;
			.tab-title {
				display: flex;
				gap: 1rem;
				button {
					color: #666;
					&.active {
						color: #000;
					}
					span {
						font-size: 1.8rem;
					}
				}
			}
		}
		.rooms-figure {
			grid-area: rooms-figure;
		}
		.rooms-text {
			grid-area: rooms-text;
			> div {
				margin-top: 1.2rem;
				&:first-child {
					margin-top: 0;
				}
				p {
					margin-top: 0.8rem;
				}
			}
		}

		.info {
			grid-area: info;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			gap: 2.4rem;
			.info-waytocome {
				img {
					margin-bottom: 1.2rem;
				}
			}
			.info-reservation {
			}
		}

		.arround {
			grid-area: around;
			&-list {
				margin-top: 1.2rem;
				display: flex;
				gap: 2.4rem;
				> li {
					display: flex;
					gap: 1rem;
				}
			}
		}

		.faq {
			grid-area: faq;
		}

		.news {
			grid-area: news;
			display: flex;
			gap: 4rem;
			border-bottom: 0.6rem double #000;
			padding: 2rem 0;
			> li {
				position: relative;
				flex: 1;
				display: flex;
				gap: 1rem;
				&:not(:last-child)::after {
					content: '';
					display: block;
					width: 0.2rem;
					height: 110%;
					position: absolute;
					right: -2rem;
					top: -5%;
					background-color: #000;
				}
				.thumb {
					@include img-crop(16rem, 16rem);
				}
				.cont {
					flex: 1;
					display: flex;
					flex-direction: column;
					justify-content: space-evenly;
					.tit {
						@include text-ellipsis(2);
					}
					.text {
						font-size: 1.4rem;
						@include text-ellipsis(5);
					}
				}
			}
		}
	}
	// ============================== 홈  끝  ==============================

	// ============================== 펜션소개 시작 ==============================
	&.about {
		grid-template-areas:
			'visual visual visual'
			'waytocome waytocome facilities';

		.visual {
			grid-area: visual;
			text-align: center;
			padding: 6rem 0 3.6rem 0;
		}

		.waytocome {
			grid-area: waytocome;
			.sub-title {
				margin-top: 1.2rem;
			}
		}

		.facilities {
			grid-area: facilities;
			.facility-list {
				display: flex;
				flex-wrap: wrap;
				gap: 8px 30px;
				text-align: center;
				li {
					width: 40px;
					display: flex;
					flex-direction: column;
					align-items: center;
					gap: 0.8rem;
					flex: 1;
					i {
						display: block;
						width: 40px;
						height: 40px;
						border: 2px solid #000;
						border-radius: 50%;
					}
				}
			}
		}
	}
	// ============================== 펜션소개  끝  ==============================

	// ============================== 객실소개 시작 ==============================
	&.rooms {
		grid-template-areas: 'map map map';

		.map {
			grid-area: map;
		}
	}
	// ============================== 객실소개  끝  ==============================

	// ============================== 펜션소식 시작 ==============================
	&.news {
		grid-template-columns: repeat(4, 1fr);
		button {
			text-align: left;
			.news-figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(2);
				height: 56px;
			}
			.date {
				color: #666;
				text-align: right;
				margin: 0.2rem;
				position: relative;
				font-size: 1.4rem;
				&::before {
					content: '';
					display: block;
					width: calc(100% - 7rem);
					height: 1px;
					border-top: 1px dashed #000;
					position: absolute;
					left: 0;
					top: calc(50% - 0.05rem);
				}
			}
			.text {
				@include text-ellipsis(6);
			}
		}
	}
	// ============================== 펜션소식  끝  ==============================

	// ============================== 예약하기 시작 ==============================
	&.reservation {
		grid-template-columns: 1fr 2fr;
		grid-template-areas:
			'caution info'
			'penalty penalty'
			'iframe iframe';
		.caution {
			grid-area: caution;
		}
		.info {
			grid-area: info;
		}
		.penalty {
			grid-area: penalty;
		}
		iframe {
			grid-area: iframe;
		}
	}
	// ============================== 예약하기  끝  ==============================

	// ============================== 주변소개 시작 ==============================
	&.around {
		grid-template-columns: repeat(4, 1fr);
		button {
			text-align: left;
			.news-figure {
				margin-bottom: 1.2rem;
			}
			h3 {
				@include text-ellipsis(1);
			}
			.source {
				color: #666;
				text-align: right;
				margin: 0.2rem;
				position: relative;
				font-size: 1.4rem;
			}
			.text {
				@include text-ellipsis(6);
			}
		}
	}
	// ============================== 주변소개  끝  ==============================

	// ============================== 자주하는질문 시작 ==============================
	&.faq {
		grid-template-areas: 'sort faqList faqList';
		.sort {
			grid-area: sort;
			button {
				display: block;
				color: #666;
				margin-bottom: 0.8rem;
				&.active {
					color: #000;
				}
				span {
					font-size: 1.8rem;
				}
			}
		}
		.faqList {
			grid-area: faqList;
			.title {
				display: flex;
				justify-content: space-between;
				align-items: flex-end;
				.btns {
					gap: 0.8rem;
					button {
						font-family: 'ChosunSm', serif;
						color: #666;
						&:last-child {
							margin-left: 0.8rem;
						}
					}
				}
			}
		}
	}
	// ============================== 자주하는질문  끝  ==============================
}
// ============================== 콘텐츠  끝  ==============================

table {
	border-top: 0.4rem double #000;
	tbody,
	thead {
		tr {
			th,
			td {
				border-bottom: 0.1rem solid #666;
				padding: 1.2rem 0.8rem;
			}
			th {
				font-family: 'ChosunSm', serif;
			}
		}
		&.alC,
		.alC {
			text-align: center;
		}
	}
}

.accord-wrap {
	display: flex;
	flex-direction: column;
	gap: 0.2rem;
	border-top: 1px solid #000;
	padding-top: 2px;
	.accord-panel {
		border-top: 0.1rem solid transparent;
		border-bottom: 1px solid #000;
		overflow: hidden;
		transition-duration: 0.5s;
		.accord-title {
			display: block;
			width: 100%;
			text-align: left;
			padding: 1rem 1.2rem 1.2rem;
			position: relative;
			&::after,
			&::before {
				content: '';
				display: block;
				position: absolute;
				background-color: #000;
				border-radius: 1px;
			}
			&::before {
				width: 0.2rem;
				height: 2rem;
				right: 2.1rem;
				top: calc(50% - 1.1rem);
				transition-duration: 0.5s;
				opacity: 1;
			}
			&::after {
				width: 2rem;
				height: 0.2rem;
				right: 1.2rem;
				top: calc(50% - 0.2rem);
			}
		}
		.accord-content {
			max-height: 0;
			background-color: #bbb;
			transition-duration: 0.5s;
			transition-timing-function: ease-in-out;
			overflow: hidden;
			p {
				padding: 2.4rem;
			}
		}
		&.active {
			border-color: #000;
			.accord-title {
				&::before {
					transform: rotate(-90deg);
				}
			}
			.accord-content {
				max-height: 200px;
			}
		}
	}
}
